<template>
  <div class="drawback-box">
    <common-header :tittle="tittle" :showleft="true" :showright="true" :routerLeft="routerLeft"></common-header>
    <div class="drawback-list" v-for="item in tabList">
      <div class="content-tab">
        <li>
          <span>订单编号：{{item.ordernumber}}</span>
          <span :style="item.status=='退款成功'?'color:#000':'#FF3F12'">{{item.status}}</span>
        </li>
        <li>
          <div class="left" >
            <img src="../../assets/image/shopcar1.png" alt="">
          </div>
          <div class="right" >
            <p>
              <span>{{item.name}}&nbsp;{{item.type}}</span>
              <span>成交价格：<i>{{item.price}}万</i></span>
            </p>
            <p>{{item.style}}</p>
            <p>{{item.dealer}}</p>
            <p><span>{{item.capacity}}</span><span>{{item.mileage}}</span></p>
            <p><span>{{item.guarantee}}</span><span>{{item.way}}</span></p>
          </div>
        </li>
        <li>
          <button class="btn" @click="refunddetail(item.id)">退款明细</button>
          <button class="btn" @click="friend" v-if="item.status=='退款办理中'">撤销申请</button>
          <router-link tag="button" :to="{name:'drawbackinfo',params:{info:item.id}}" class="btn">查看详情</router-link>
        </li>

      </div>
    </div>

  </div>
</template>

<script>
import imgsrc from '../../assets/image/shopcar1.png'
import commonHeader from 'common/common-header'

export default {
  name: 'drawback',
  components: {
    commonHeader
  },
  data() {
    return {
      tittle: '退款列表',
      routerLeft: '/day',
      show: true,
      tabList: [
        {
          id: '1',
          ordernumber: 'BC12345678976',
          status: '退款办理中',
          img: imgsrc,
          name: '尼欧 ||',
          type: '类型',
          price: '5.8万',
          style: '2018款/黑色/款式',
          dealer: '经销商：xxx经销商',
          capacity: '容量：2.5L',
          mileage: '续航里程：50公里',
          guarantee: '保修政策',
          way: '金融方式'
        },
        {
          id: '2',
          ordernumber: 'BC12345678976',
          status: '退款成功',
          img: imgsrc,
          name: '尼欧 ||',
          type: '类型',
          price: '5.8万',
          style: '2018款/黑色/款式',
          dealer: '经销商：xxx经销商',
          capacity: '容量：2.5L',
          mileage: '续航里程：50公里',
          guarantee: '保修政策',
          way: '金融方式'
        }
      ],
      order1List: []
    }
  },
  mounted() {
  },
  methods: {
    friend() {
      console.log('敬请期待')
    },
    refunddetail(index) {
      this.$router.push('/drawbackinfo')
      console.log(index)
    }
  }
}
</script>

<style scoped lang="less">
  @import "~styles/index.less";
  @import "~styles/variable.less";
  .drawback-box{
    width: 100%;
    height: auto;


    .drawback-header{
      width: 100%;
      height: 1.35rem;
      border-bottom: 1px solid #F3F3F3;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #fff;
      .fs(35);
      li{
        padding-left: 0.2rem;
        padding-right: 0.2rem;
        img:nth-child(1){
          width: 0.3rem;
          height: 0.5rem;
        }
        img:nth-child(2){
          width: 0.6rem;
          height: 0.5rem;
        }
      }

    }
    .drawback-list{
      padding: 0 0.4rem;
      .content-tab{
        border-radius:5px;
        background: #fff;
        padding: 0 0.4rem;
        .mb(20);
        margin-top: 0.4rem;
        li:nth-child(1){
          width: 100%;
          display: flex;
          justify-content: space-between;
          background: #fff;
          .fs(30);
          padding-top: 0.4rem;
          /*padding-bottom: 0.2rem;*/
          span:nth-child(1){
            color:#9E9E9E ;
          }
          span:nth-child(2){
            color:#2ED36B ;
          }
        }
        li:nth-child(2){
          color: #989898;
          height: auto;
          display: flex;
          justify-content: space-between;
          background: #fff;
          align-items: center;
          margin-top: 0.3rem;
          height: 3rem;
          border-bottom: 1px solid #F5F5F5;
          padding-bottom: 0.2rem;
          .left{
            width: 30%;
            height: 100%;
            img{
              width: 100%;
              height: 100%;
              border-radius: 0.2rem;
              vertical-align: middle;
            }
          }
          .right{
            width: 70%;
            text-align: left;
            padding-left: 0.3rem;
            line-height: 0.6rem;
            .fs(30);
            p{
              .fs(28);
              //letter-spacing: 0.01rem;
            }
            p:nth-child(1){
              display: flex;
              justify-content: space-between;
              span:nth-child(2){
                i{
                  color: #FF3F12;
                  font-weight: 600;
                  font-style:normal
                }
              }
              span:nth-child(1){
                color: #000;
                .fs(33);
              }
            }
            p:nth-child(4){
              display: flex;
              justify-content: space-between;
              /*span:last-child{
                margin-left: 0.6rem;
              }*/
            }
            p:nth-child(5){
              span:last-child{
                margin-left: 0.9rem;
              }
            }
          }
        }
        li:nth-child(3){
          margin-top: 0.3rem;
          display: flex;
          justify-content: flex-end;
          .pb(30);
          .btn{
            .fs(28);
            background: #fff;
            border: 1px solid #F68151;
            border-radius: 4px;
            .padding(10,24);
          }
          .btn:nth-child(1){
            background: #F45416;
            color: #fff;
          }
          .btn:nth-child(2){
            color: #F45416;
            margin-left: 0.3rem;
            margin-right: 0.3rem;
          }
          .btn:nth-child(3){
            color: #F45416;
          }
        }
      }
    }
  }
</style>



